import "material-design-lite/dist/material.min";
import "material-design-lite/dist/material.min.css";
import "material-design-lite/dist/material.grey-pink.min.css";
import "./css/font.css";
import "./css/icon.css";
import "./css/styles.css";
import { of, from, fromEvent, withLatestFrom } from "rxjs";
import { map, filter, mergeMap } from "rxjs/operators";
import Environment from "./Environment";
import AppManager from "./AppManager";

fromEvent(document, "DOMContentLoaded")
  .pipe(
    withLatestFrom(of("./assets/apps.json?220311")),
    map((x) => x.slice(-1)[0]),
    mergeMap((url) =>
      fetch(url)
        .then((response) => {
          if (response.ok) {
            return response.json();
          } else {
            return of({ error: true, message: `Error ${response.status}` });
          }
        })
        .catch((err) => {
          console.error(err);
          return of({ error: true, message: err.message });
        })
    )
  )
  .subscribe((result) => {
    Environment.setUp();

    // JAX-RS serializes an empty list as null, and a 'collection of one' as an object (not an 'array of one')
    let list = [];

    if (result?.apps === null) {
      list = [];
    } else if (result?.apps instanceof Array) {
      list = result?.apps;
    } else {
      list = [result?.apps];
    }

    const apps$ = from(list).pipe(filter((app) => app?.isUse));
    const appId = AppManager.getParameter(location?.href, "id");

    if (appId) {
      apps$
        .pipe(
          filter((app) => app?.id === appId),
          mergeMap((app) => AppManager.createAppDetail$(app)),
          withLatestFrom(of(document.getElementById("app_detail")))
        )
        .subscribe(([html, detail]) => {
          detail.innerHTML = html;
        });
    } else {
      apps$
        .pipe(
          map(AppManager.createAppItem),
          withLatestFrom(of(document.getElementById("app_detail")))
        )
        .subscribe(([item, detail]) => {
          detail.appendChild(item);
        });
    }

    const config = result?.environment;
    document.title = document.title + (config ? ` (${config})` : "");
  });
