import FaviconLoader from "./FaviconLoader";

function setFavicon() {
  const url = process.env.FAVICON_URL;
  if (url) {
    FaviconLoader(process.env.FAVICON_URL);
  }
}

function setTitleLogo() {
  const logoClassName = process.env.TITLE_LOGO_CLASSNAME;
  if (logoClassName) {
    document.getElementById("title-logo").classList.add(logoClassName);
  }
}

export default {
  setUp: () => {
    setFavicon();
    setTitleLogo();
  },
};
