import { from, zip } from "rxjs";
import { map, find, take, reduce } from "rxjs/operators";

const AppManager = {
  getParameter: (url, param) => {
    let returnValue;
    const parameters = url.slice(url.indexOf("?") + 1, url.length).split("&");
    for (let parameter of parameters) {
      const varName = parameter.split("=")[0];
      if (varName.toUpperCase() == param.toUpperCase()) {
        returnValue = parameter.split("=")[1];
        return decodeURIComponent(returnValue);
      }
    }
  },

  createAppItem: (app) => {
    const item = document.createElement("div");
    item.className = "mdl-cell mdl-card mdl-shadow--4dp portfolio-card";
    item.innerHTML = `
      <div class="mdl-card__media">
        <img class="article-image" src="${app?.indexImageUrl}" border="0" alt="">
      </div>
      <div class="mdl-card__title">
        <h2 class="mdl-card__title-text">${app?.name}</h2>
      </div>
      <div class="mdl-card__supporting-text">${app?.summary}</div>
      <div class="mdl-card__actions mdl-card--border">
        <a class="mdl-button mdl-button--colored mdl-js-button mdl-js-ripple-effect mdl-button--accent"
          href="index.html?id=${app?.id}">Read more</a>
      </div>
    `;
    return item;
  },

  createAppDetail$: (app) => {
    let downloadHtml$ = AppManager.createDownloadHtml$(app?.downloads).pipe(
      reduce((acc, curr) => acc + curr, "")
    );

    let screenshotHtml$ = AppManager.createScreenshotHtml$(
      app?.screenshots
    ).pipe(reduce((acc, curr) => acc + curr, ""));

    let historyHtml$ = AppManager.createHistoryHtml$(app?.histories).pipe(
      reduce((acc, curr) => acc + curr, "")
    );

    return zip(downloadHtml$, screenshotHtml$, historyHtml$).pipe(
      map(
        ([downloadHtml, screenshotHtml, historyHtml]) => `
          <div class="mdl-cell mdl-cell--12-col mdl-card mdl-shadow--4dp">
            <!-- App Information -->
            <div class="mdl-grid portfolio-copy">
              <div class="mdl-cell mdl-cell--4-col mdl-cell--2-col-phone">
                <img class="article-image" style="border-radius:20px; width: 100px; height: auto;" src="${app?.iconUrl}" border="0" alt="">
              </div>
              <div class="mdl-cell mdl-cell--8-col mdl-cell--2-col-phone">
                <h2 class="mdl-card__title-text">${app?.name}</h2>
                <p>${app?.summary}</p>
              </div>
              <div class="mdl-cell mdl-cell--12-col">
                ${downloadHtml}
              </div>
              <!-- Screenshot -->
              <h3 class="mdl-cell mdl-cell--12-col mdl-typography--headline">Screenshot</h3>
              ${screenshotHtml}
              <!-- Description -->
              <h3 class="mdl-cell mdl-cell--12-col mdl-typography--headline">Description</h3>
              <div class="mdl-cell mdl-cell--12-col mdl-card__supporting-text no-padding">
                <p>${app?.description}</p>
              </div>
              <!-- What&#039;s new -->
              <h3 class="mdl-cell mdl-cell--12-col mdl-typography--headline">What&#039;s new</h3>
              <div class="mdl-cell mdl-cell--12-col mdl-card__supporting-text no-padding">
                <!-- Information -->
                ${historyHtml}
              </div>
            </div>
          </div>`
      )
    );
  },

  createDownloadHtml$: (downloads) => {
    let currentPlatform;

    if (
      /iPhone|iPad|iPod/i.test(window.navigator.userAgent) ||
      (/Macintosh/i.test(window.navigator.userAgent) &&
        navigator.maxTouchPoints === 5)
    ) {
      currentPlatform = "IOS";
    } else if (/Android/i.test(window.navigator.userAgent)) {
      currentPlatform = "ANDROID";
    } else if (/Windows/i.test(window.navigator.userAgent)) {
      currentPlatform = "WINDOWS";
    } else {
      currentPlatform = "Unkown";
    }

    return from(downloads).pipe(
      find(
        (download) =>
          download?.platform.toUpperCase() === currentPlatform.toUpperCase()
      ),
      map((download) => {
        const attr = download
          ? `href="${download?.url}"`
          : `disabled="disabled"`;
        const text = download ? "Install" : "Not available";
        return `
            <a class="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--accent"
              style="float: right;" ${attr}>
              ${text}
            </a>`;
      })
    );
  },

  createScreenshotHtml$: (screenshots) => {
    return from(screenshots).pipe(
      map((screenshot) => {
        const orientation = screenshot?.orientation ?? "portrait";

        return `
          <div class="mdl-cell ${
            orientation === "landscape" ? "mdl-cell--6-col" : "mdl-cell--3-col"
          } mdl-cell--1-col-phone">
            <img class="article-image" src="${
              screenshot?.url
            }" border="0" alt="${screenshot?.alt}">
          </div>`;
      })
    );
  },

  createHistoryHtml$: (histories) => {
    return from(histories).pipe(
      map(
        (history) =>
          `<p><b>${history.version}</b><br/>${history.description}</p>`
      ),
      take(3)
    );
  },
};

export default AppManager;
